/* src/pages/HorrorBooks.css */

.horror-books {
  padding: 20px;
  text-align: center;
}

.book-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.book-card {
  width: 200px;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  transition: transform 0.2s;
}

.book-card:hover {
  transform: scale(1.05);
}

.book-card img {
  width: 100%;
  border-radius: 4px;
}

.book-card h3 {
  font-size: 1.1em;
  margin-top: 10px;
}

.more-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  background-color: #333;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s;
}

.more-button:hover {
  background-color: #555;
}
